\n  font-style: normal;\n  font-weight: 400 800;\n  src:\n    url('../assets/font/Raleway-VariableFont_wght.woff2') format('woff2');\n}\n\n*,\n*::before,\n*::after{\n  margin: 0;\n  padding: 0;\n  box-sizing: inherit;\n}\n\nbutton {\n  font-size: inherit;\n  font-family: inherit;\n  font-weight: inherit;\n}\n\nhtml {\n  font-size: 62.5%;\n  font-family: \"Raleway\", sans-serif;\n  // font-family: Helvetica;\n  text-rendering: optimizeLegibility;\n  \n  //breakpoints for reasonable resizing of fonts on other devices\n  @include mixins.respond(tab-land){  // width < 1200\n    font-size: 56.25%;\n  }\n  @include mixins.respond(tab-port){  // width < 900?\n    font-size: 50%;\n  }\n  @include mixins.respond(phone){ // width ~ 400\n    font-size: 40%;\n    // letter-spacing: 0.01rem;\n  }\n  \n  @include mixins.respond(desktop){\n    font-size: 66%;\n  }\n  @include mixins.respond(big-desk){  // width > 1800\n    font-size: 75%;\n  }\n}\n\n\nli {\n  list-style: none; // because list style is dumb\n}\n\n.app-wrapper {\n  display: flex;\n  flex-flow: column nowrap;\n  align-items: center;\n\n}\n\n","@use '../abstracts/vars';\n\n// generalized responsive design breakpoints\n@mixin respond($breakpoint){\n  @if $breakpoint == small-phone {\n    @media screen and (max-width: 25em) { @content };\n  }\n  @if $breakpoint == phone {\n    @media screen and (max-width: 37.5em) { @content }; // 600px\n  }\n  @if $breakpoint == tab-port {\n    @media screen and (max-width: 56.25em){ @content }; // 900px | 1em = 38.4px\n  }\n  @if $breakpoint == tab-land {\n    @media screen and (max-width: 75em) { @content }; // up to 750px? 1200px | 1em = 48px\n  }\n  @if $breakpoint == desktop { // 760 - 1120?\n    @media (min-width: 76em) and (max-width: 112em){ @content };\n  }\n  @if $breakpoint == m-desktop { //1130 - 1240?\n    @media (min-width: 113em ) and (max-width: 124em){ @content };\n  }\n  @if $breakpoint == big-desk {\n    @media screen and (min-width: 125em) { @content }; // 1800px?\n  }\n}\n\n@mixin fontShadow(){\n  text-shadow: \n    0.3rem 0.3rem vars.$button-font-shadow, \n    -1px 0 1px vars.$button-font-outline, \n    0 -1px 1px vars.$button-font-outline;\n}\n\n@mixin newHeadingShadow(){\n  color: white;\n  text-shadow: 0.06em 0em 0.03em vars.$goth-lavender, 0.1em 0 vars.$potential-blue;\n}\n\n@mixin desktopPseudoDivider(){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1.5rem;\n\n  border: 0.3rem solid vars.$portland-red;\n  height: 0.2rem;\n  width: 88%;\n  margin: -3rem -44%;\n}\n\n@mixin mobilePseudoDivider(){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1rem;\n\n  border: 0.4rem solid vars.$portland-red;\n  height: 0.3rem;\n  width: 90%;\n  margin: -2rem -45%;\n}\n\n@mixin singleLinePseudoDivider($width){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1rem;\n\n  border-top: 0.4rem solid vars.$portland-red;  \n  width: vars.$width or 90%;\n  margin: -2rem (-vars.$width/2 or -45%);\n}\n\n@mixin sectionBorderTop(){\n  border-top: 3px solid vars.$portland-red;\n}\n\n// offset-x | offset-y | blur-radius | spread-radius | color \n      // box-shadow: 0.2rem 0rem 0.1rem 0rem rgba(0,0,0,0.3);","/*\nIn this file:\n- any property with \"font\" in the name\n- any property with \"text\" in the name\n- color\n- line-height\n*/\n\n@use \"./vars\";\n@use \"./mixins\";\n\n.nav-hat {\n  .full-nav {\n\n    a {\n      text-decoration: none;\n      font-weight: bold;\n      font-size: 3rem;\n      @include mixins.newHeadingShadow();\n    }\n    a:link {     color: #dedede; }\n    a:visited {  color: #d4d4d4; }\n    a:hover {    color: #d1d1d1; }\n    a:active {   color: #f7f7f7; }\n    a:focus {    color: #f7f7f7; }\n  }\n\n  .mobile-nav {\n\n    a {\n      text-decoration: none;\n      font-weight: bold;\n      font-size: 3rem;\n      @include mixins.newHeadingShadow();\n    }\n    a:link {     color: #dedede; }\n    a:visited {  color: #d4d4d4; }\n    a:hover {    color: #d1d1d1; }\n    a:active {   color: #f