-size: 0.7em; // todo - change this?\n\n          @include mixins.respond(tab-port) {\n            font-size: 1em;\n          }\n        }\n\n        .item-size {\n          color: vars.$dark-font;\n          font-weight: 400;\n          font-size: 0.7em;\n        }\n      }\n    }\n  }\n}\n\n.packaged-section {\n  .packaged-formats {\n    font-size: 0.7em;\n  }\n}\n\n.redwine-item-name.item-name,\n.whitewine-item-name.item-name,\n.bubbles-item-name.item-name {\n  font-size: 0.75em;\n\n  @include mixins.respond(phone) {\n    font-size: 1em;\n  }\n}\n\n.redwine-item-price.item-price,\n.whitewine-item-price.item-price,\n.bubbles-item-price.item-price {\n  @include mixins.respond(phone) {\n    font-size: 1em;\n  }\n}\n\n.wine-sizes {\n  font-size: 0.6em;\n  text-align: end;\n  letter-spacing: 0.12rem;\n\n  @include mixins.respond(phone) {\n    font-size: 0.9em;\n  }\n}\n\n.wine-item-varietal {\n  font-size: 0.75em;\n\n  @include mixins.respond(phone) {\n    font-size: 0.9em;\n  }\n}\n\n.wine-item-region {\n  font-size: 0.9em;\n}\n\n.nonalc-item.menu-item {\n  .nonalc-item-name.item-name {\n    font-size: 0.65em;\n\n    @include mixins.respond(phone) {\n      font-size: 1em;\n    }\n  }\n}","$light-font-visited: #dddddd;\n$light-font-hover: #ffffff;\n\n$alt-dark-font: #201713;\n$dark-font: #2d2d2d;\n$soft-black: #424242;\n$button-font-shadow: #787878;\n$button-font-size: 2.5rem;\n$button-font-outline: #787878e6;\n\n$bright-brown: #9e4334;\n$less-bright-brown: #a0170f;\n$medium-brown: #94241e;\n$brown-brown: #6a2008;\n\n$menu-background-color: #f7f3e3;\n\n$portland-beige: #babd9b;\n$portland-off-white: #efeeec;\n$portland-red: #bd141b;\n$portland-red-alpha: #bd141ad7;\n\n//////////////////////////////////\n/////// NEW COLOR SCHEMES\n$portland-mustard: #bdb714;\n$dull-orange: #bd6314;\n\n$portland-pink: #bd146e;\n$portland-dull-red: #a33236;\n$potential-blue: #146ebd;\n\n$maybe-green: #70bd14;\n\n/////////////////////// \n//// try this \n// #bd141b, #f7f3e3, #ecf0f1, #2b2