ace-between;\n  align-items: center;\n\n  .nav-bknd-img-wrapper {\n    position: absolute;\n    width: 100%;\n    height: vars.$desktop-nav-height;\n    overflow: hidden;\n  \n    @include mixins.respond(big-desk) {\n      background-color: purple;\n    }\n  \n    img {\n      object-fit: cover;\n      width: 110%;\n      filter: brightness(45%);\n      transform: translate(-10rem, -52rem) rotate(-12deg);\n  \n      @include mixins.respond(desktop) {\n        transform: translate(-10rem, -58rem) rotate(-12deg);\n      }\n    }\n  }\n\n  .nav-logo {\n    position: relative;\n    height: auto;\n    width: auto;\n    margin-top: 0;\n    margin-right: vars.$desktop-side-margin / 2;\n    margin-bottom: 0;\n    margin-left: vars.$desktop-side-margin;\n  }\n\n  .button-group {\n    position: relative;\n    margin-top: 0;\n    margin-right: vars.$desktop-side-margin;\n    margin-bottom: 0;\n    margin-left: vars.$desktop-side-margin;\n    width: 100%;\n\n    display: flex;\n    flex-flow: row;\n    justify-content: space-between;\n  }\n}\n","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.nav-hat .mobile-nav{\n  position: fixed;\n  top: 0;\n  width: 100vw;\n  height: vars.$mobile-nav-height;\n\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: stretch;\n  align-items: center;\n  \n  background-color: vars.$alt-dark-font;\n\n  .nav-logo{\n    display: inline-block;\n    height: vars.$mobile-nav-height - 1.8rem;\n    // margin: 0 0 0 vars.$mobile-side-margin;\n\n    background-color: vars.$alt-dark-font;\n\n    @include mixins.respond(phone){\n      height: vars.$phone-nav-logo-height;\n      // margin: 0.5rem 0 0.5rem vars.$phone-side-margin;\n    }\n\n    @include mixins.respond(small-phone){\n      height: vars.$phone-nav-logo-height - 0.8rem;\n      margin: 0.5rem 0 0.5rem (vars.$phone-side-margin)-0.5rem;\n    }\n\n    img{\n      height: 100%;\n    }\n  } \n\n  .button-group{\n    display: flex;\n    flex-flow: row nowrap;\n    align-items: center;\n    justify-content: space-between;\n    width: 100%;\n    height: 100%;\n\n    // // todo - are these necessary?\n    // @include mixins.respond(tab-land){\n    //   margin: 0 (vars.$mobile-side-margin + 1rem );\n    // }\n    \n    // @include mixins.respond(phone){\n    //   margin-right: vars.$phone-side-margin / 2;\n    //   margin-left: vars.$phone-side-margin / 2;\n    // }\n\n    // @include mixins.respond(small-phone){\n    //   margin-left: vars.$phone-side-margin;\n    //   margin-right: vars.$phone-side-margin - 0.5rem;\n    // }\n    \n    button {\n\n      background-color: vars.$alt-dark-font;\n\n      @include mixins.respond(phone) {\n        margin-left: 0.5rem;\n        margin-top: 0.6rem;\n\n      }\n\n     \n    }\n  }\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.footer{\n  position: relative;\n  top: vars.$desktop-nav-height; //if this changes, content needs adjusting too\n  width: 76vw;\n  margin: 0 auto;\n\n  padding: 3rem;\n  display: flex;\n  flex-flow: column;\n  align-items: flex-start;\n\n  justify-content: end;\n\n  font-size: 1.3rem;\n\n  @include mixins.respond(tab-port){\n    // width: 100%;\n    padding: 2rem;\n\n    flex-flow: column;\n    align-items: stretch;\n    justify-content: space-between;\n  }\n\n  & a {\n    color: vars.$brown-q;\n    text-decoration: none;\n  }\n  & a:link {\n    color: vars.$brown-q;\n  }\n  & a:visited {\n    color: vars.$brown-q;\n  }\n  & a:hover {\n    color: vars.$brown-q;\n  }\n  & a:active {\n    color: vars.$brown-q;\n  }\n  & a:focus {\n    color: vars.$brown-q;\n  }\n\n  & div {\n    margin-top: 1rem;\n  }\n\n  &-address {\n    font-size: 1.3em;\n  }\n\n  &-social {\n\n    display: flex;\n    flex-flow: row;\n    align-items: center;\n\n    @include mixins.respond(tab-port){\n      align-items: flex-start;\n    }\n\n\n    &-link {\n\n      margin-right: 2rem;\n  \n      img {\n        height: 3rem;\n      }\n    } \n  }\n\n  &-phonenumber {\n    margin-bottom: 1rem;\n    font-size: 1.5em;\n  }\n\n  &-author-info {\n    margin-top: 1rem;\n  }\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.cocktail-section{\n  grid-column: 1;\n  grid-row: 3;\n\n  display: grid;\n  grid-template-columns: 3fr 4fr 3fr;\n  grid-template-rows: 7rem auto;\n\n  border-top: 3px solid vars.$portland-red;\n\n  @include mixins.respond(tab-port){\n    grid-column: 1 / span 7;\n    grid-row: 2 / span 2;\n\n    width: 99%; // WHAT, WHY, THIS IS SUS\n  }\n\n  &-title {\n    grid-column: 2;\n    grid-row: 1;\n    justify-self: center;\n  }\n\n  .cocktail-item {\n    display: grid;\n    grid-template-columns: 8fr 1.5fr;\n    grid-template-rows: auto auto;\n    margin: 0.5rem 0;\n\n    @include mixins.respond(phone) {\n      margin-top: 1rem;\n      margin-bottom: 1rem;\n    }\n\n    &-name{\n      grid-column: 1;\n      grid-row: 1;\n    }\n\n    &-price{\n      grid-column: 2;\n      grid-row: 1 / span 2;\n      justify-self: end;\n    }\n\n    &-notes{\n      grid-column: 1 / span 2;\n      grid-row: 2;\n    }\n  }\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n////////////////////////////////////////////////\n//////////// FOOD SECTION\n.food-section {\n  \n  position: relative;\n  margin-bottom: 1rem;\n\n  min-width: 45%;\n  \n  display: flex;\n  flex-flow: column;\n\n  border-top: 3px solid vars.$portland-red;\n  \n  @include mixins.respond(tab-port){\n    margin-bottom: 1.5rem;\n    align-items: stretch;\n  }\n\n  .section-title {\n\n  }\n\n  .item-list {\n    display: flex;\n    flex-flow: row wrap;\n    padding-left: 2rem;\n\n    @include mixins.respond(tab-port){\n      flex-flow: column nowrap;\n      justify-content: space-around;\n      align-items: stretch;\n      padding-left: 0;  \n    }\n    \n    @include mixins.respond(phone){\n    \n      flex-flow: column nowrap;\n      align-items: stretch;\n    } \n  }\n}\n///////////////////////////////////////\n////////// SWEETS\n.sweets {\n  .item-list {\n    grid-template-colum